#load{
  width:100%;
  height: 100vh;
  position:relative;
  #loadAnim{
    position: relative;
    top:50%;
    max-width: 100%;
    height:auto;
    transform: translateY(-50%);
      #SVGOPAnim{
        filter: drop-shadow(0 0 5px #09fbd3) drop-shadow(0 0 10px #09fbd3);
      }
  }
}

@keyframes strokeAnimation{
  from{
    stroke-opacity: 1;
  }
  to{
    stroke-opacity: 0;
  }
}